@import url('https://fonts.googleapis.com/css2?family=Concert+One&display=swap');

.cabecalho{
    display:flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(156, 218, 98);
}
.titulo {
    font-size: 3rem;
    color: rgb(0, 0, 0);
    margin: 15px;
    font-family: 'Concert One', cursive;
}
.subtitulo{
    font-size: 2rem;
}
img{
    width: 50px;
}
img:hover{
    animation: rotaciona 2s infinite linear;
}

@keyframes rotaciona {
    0% {
      transform: rotate(0deg);
    }
    25% {
      transform: rotate(10deg);
    }
    75%{
        transform: rotate(-10deg);
    }
    100%{
        transform: rotate(0deg);
    }
  }